.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#cover {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

#content {
  top: 0;
  width: 100vw;
  min-height: 100vh;
  color: white;
  z-index: 10;
  position: absolute;
}

.page {
  margin: 5vw 3vw;
  padding: 1px 2vw;
  width: 90vw;
}

.page:first-of-type {
  min-height: 100vh;
  margin-top: 0;
  text-align: center;
}

.shadow {
  text-shadow: 0 3px 8px #000000;
}

body::-webkit-scrollbar-track
{
  background-color: rgb(16, 16, 16);
}

body::-webkit-scrollbar
{
  width: 12px;
  background-color: #F5F5F5;
}

body::-webkit-scrollbar-thumb
{
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #555;
}

#title {
  font-size: 3em;
  margin-bottom: 2vh;
  font-weight: lighter;
}

#subtitle {
  font-size: 1.5em;
  font-style: italic;
}

#title-box {
  text-align: center;
  display: inline-block;
  margin-top: 50vh;
  -webkit-transform: translateY(-60%);
      -ms-transform: translateY(-60%);
          transform: translateY(-60%);
  text-shadow: 0 4px 10px #000000;
}

#contact-box {
  margin: 0 auto;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.1);
}

#contact-image {
  width: 100px;
  margin-right: 15px;
}

#contact-name {
  margin: 0;
}

#contact-address {
  margin-top: 0;
  font-weight: lighter;
  font-style: italic;
}

h2 {
  font-weight: lighter;
  font-style: italic;
}



